<template>
    <nav v-if="pageCount > 1" aria-label="Page navigation">
        <ul class="pagination">
            <li class="first" :class="{disabled: (currentPage === 1)}"><a href="#" @click.prevent="navigate(1)" :disabled="(currentPage === 1)">&lt;&lt;</a></li>
            <template v-if="size === 1">
                <li class="previous" :class="{disabled: (currentPage === 1)}"><a href="#" @click.prevent="navigate(currentPage - 1)" :disabled="(currentPage === 1)">&lt;</a></li>
                <li class="disabled"><a disabled="1"><template v-if="!['xs', 'xxs'].includes($mq)">{{ (lang === 'de' ? 'Seite' : 'Page') }} </template>{{ currentPage }} {{ lang === 'fr' ? 'sur' : (lang === 'de' ? 'von' : 'of') }} {{ pageCount }}</a></li>
                <li class="next" :class="{disabled: (currentPage + 1 > pageCount)}"><a href="#" @click.prevent="navigate(currentPage + 1)" :disabled="(currentPage + 1 > pageCount)">&gt;</a></li>
            </template>
            <template v-else>
                <li v-for="page in pageRange" :key="page" :class="{active: page === currentPage}" :data-page="page"><a href="#" @click.prevent="navigate(page)">{{ page }}</a></li>
            </template>
            <li class="last" :class="{disabled: (currentPage >= pageCount)}"><a href="#" @click.prevent="navigate(pageCount)" :disabled="(currentPage >= pageCount)">&gt;&gt;</a></li>
        </ul>
    </nav>
</template>

<script>
export default {
    props: {
        lang: String,
        currentPage: {
            type: Number,
            required: true,
            validator: function (value) {
                return value > 0;
            }
        },
        pageCount: {
            type: Number,
            required: true,
            validator: function (value) {
                return value >= 0;
            }
        },
        size: {
            type: Number,
            required: false,
            default: 15,
            validator: function (value) {
                // Size should be an odd number
                return value % 2 === 1;
            }
        }
    },

    computed: {
        pageRange () {
            let lowerBound = Math.max(1, this.currentPage - Math.floor(this.size / 2));
            lowerBound = Math.max(1, Math.min(lowerBound, this.pageCount - this.size + 1));

            // Construct an array starting at lowerBound up to and including
            // (lowerBound + this.size - 1):
            // [lowerBound, lowerBound + 1, ..., lowerBound + 13, lowerBound + 14]
            // See https://stackoverflow.com/questions/18947892/creating-range-in-javascript-strange-syntax
            const range = Array.apply(null, { length: Math.min(this.pageCount, this.size) })
                .map(function (_, index) { return index + lowerBound; });
            return range;
        }
    },

    methods: {
        navigate (page) {
            if (page > this.pageCount || page === this.currentPage || page <= 0) {
                return;
            }

            this.$emit('navigate', page);
        },
    },

    data () {
        return {};
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';

nav {
    text-align: center;
}

nav .pagination /deep/ {
    font-size: 0.9em;

    @include media-breakpoint-down(md) {
        .pagination {
            a {
                padding: 4px 9px;
            }
        }
    };

    @include media-breakpoint-down(xs) {
        .pagination {
            font-size: 0.8em;
            margin: 1em 0;
            a {
                padding: 3px 6px;
                &.active {
                    color: white;
                }
            }
        }
    };

    @include media-breakpoint-down(xxs) {
        a, span {
            padding: 3px 6px;
        }
    };
}
</style>



